import styled from "styled-components";
import {AiOutlineArrowRight , AiOutlineRight} from 'react-icons/ai' 
export const HeroContainer = styled.div `
background-color: #0c0c0c;
display: flex;
justify-content: center;
align-items: center;
padding: 0 30px;
height: 800px;
position: relative;
z-index: 1;
:before {
     content: '';
     position: absolute;
     top: 0;
     left: 0;
     bottom: 0;
     right: 0;
     background: linear-gradient(180deg,rgba(0,0,0,.2)0%,
     rgba(0,0,0,.6) 100%), linear-gradient(180deg,rgba(0,0,0,.2)0%, transparent);
    z-index: 2;
 }

`;

/* add :before styles */

export const HeroBg = styled.div `
position: absolute;
top: 0;
right: 0;
bottom: 0;
left: 0;
width: 100%;
height: 100%;
overflow: hidden;
`;
export const VideoBg = styled.video `
width: 100%;
height: 100%;
object-fit: cover;
background: #232a34;
`;
export const HeroContent = styled.div `
z-index: 3;
max-width: 600px;
position: absolute;
padding: 8px 24px;
display: flex;
flex-direction: column;
align-items: center;
`;

export const HeroH1 = styled.h1 `
color: #fff;
font-size: 40px;
text-align: center;

@media (max-width:768px){
    font-size: 24px;
}
@media (max-width:480px){
    font-size: 18px;
}
`;
export const HeroP = styled.p `
margin-top: 24px;
color: #fff;
font-size: 24px;
text-align:center;
max-width: 600px;
`;
export const HeroBtn = styled.div `
margin-top: 32px;
display: flex;
flex-direction: column;
align-items: center;
`;

export const ArrowForward = styled(AiOutlineArrowRight)`
margin-left: 8px;
font-size: 20px;
`;
export const ArrowRight = styled(AiOutlineRight)`
margin-left: 8px;
font-size: 20px;
`;
